/* Input container */
.input-container {
  position: relative;
  margin: 20px;
}

/* Input field */
.input-field {
  display: block;
  width: 300px;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #ccc;
  outline: none;
  background-color: transparent;
}
.input-field2 {
  display: block;
  width: 300px;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #00090E;
  outline: none;
  background-color: transparent;
}
@media (max-width:767px) {
