   }
   
 }
 .collab{
  border-radius: 10px;
  border: 1px solid #B3CCFF;
  width: max-content;
 }

/* Input label */
.input-label {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 16px;
  color: rgba(204, 204, 204, 0);
  pointer-events: none;
  transition: all 0.3s ease;
}

/* Input highlight */
.input-highlight {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 0;
  background-color: #66ADCC;
  transition: all 0.3s ease;
}
.input-highlight2 {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 0;
  background-color: #00090E;
  transition: all 0.3s ease;
}

/* Input field:focus styles */
.input-field:focus + .input-label {
  top: -20px;
  font-size: 12px;
  color: #66ADCC;
}
.input-field2:focus + .input-label2 {
  top: -20px;
  font-size: 12px;
  color: #00090E;
}
.input-field:focus + .input-label,
.input-field:not(:placeholder-shown) + .input-label {
  color: #090909; /* Change text color to white */
}
::-ms-input-label { /* Edge 12-18 */
  color: #00090E;
}
.input-field:focus + .input-label + .input-highlight {
  width: 100%;
}
